
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#radio {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
input {
  margin-left: 2em;
}
label {
  font-size: 2em;
}
td {
  width: 33%;
  height: 33%;
  cursor: pointer;
}
td + td {
  border-left: 4px solid;
  border-color: #ccdfcb;
}
tr + tr {
  border-top: 4px solid;
  border-color: #ccdfcb;
}
.inactive {
  pointer-events: none;
}
table {
  background-color: #414141;
  width: 90%;
  margin-left: 5%;
  height: 800px;
  border-collapse: collapse;
  color: #ccdfcb;
  font-size: 10em;
}




